    0%{
        
    }
    100%{
        opacity: 1;
    }
}

@keyframes slide{
    0%{
        
    }
    100%{
        opacity: 1;
    }
}
*{
  box-sizing: border-box;
}

html{
  width: 100vw;
  scroll-behavior: smooth;
}
.wrap{
  height: 100vh;
  width: 100vw;
  margin: auto;
}

.App{
  margin-top: 40px;
  width: 100%;
}

.App img{
  height: 100px;
  width: 120px;
}

.menu div{
  height: 4px;
  width: 24px;
  background-color: white;
  
}

.nav p{
  color: white;
  font-family: 'Nunito', sans-serif;
  font-weight: 800;
  font-size: 20px;
}

.nav{
  display: flex;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100vw;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  background-color: rgb(42, 23, 43);
  box-shadow: 0px 0.2px 8px 0.2px #252525;
  padding-left: 14px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}

.nav div,.nav p{
  margin: 4px;
}

.texts{
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100vw;
  padding-bottom: 50px;
  padding-top: 20px;
}


.input{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height:50px;
  width:100vw;
  background-color: rgb(238, 237, 237);
  box-shadow: 0px 0.2px 6px 0.2px #727171;
  position: fixed;
  bottom: 0;
  left: 0;
  padding-right: 8px;
  padding-left: 8px;
}

.top{
  display: flex;
  align-items: center;
}

.bottom{
  background-color: rgb(42, 23, 43);
  padding: 10px 12px;
  border-radius: 50%;
  -webkit-animation: circle 1s 1 3.5s forwards ;
          animation: circle 1s 1 3.5s forwards ;
}

.input p{
  margin-left: 15px;
  border: 2px solid #cccccc;
  border-radius: 10px;
  padding: 10px 8px;
  width: 240px;
  height: 41px;
  overflow: hidden;
  -webkit-animation:text 1s 1 3s backwards ;
          animation:text 1s 1 3s backwards ;
  opacity: 0;
}

.camera{
  color: rgb(42, 23, 43);
  height: 30px;
}

@-webkit-keyframes circle{
  0%{
    opacity:1 ;
 }
 100%{
   opacity:0.2;
}
}

@keyframes circle{
  0%{
    opacity:1 ;
 }
 100%{
   opacity:0.2;
}
}

@-webkit-keyframes text{
  0%{
    opacity: 0;
    width: 0;

 }
 100%{
  width: 100%;
  opacity: 1;
}
}

@keyframes text{
  0%{
    opacity: 0;
    width: 0;

 }
 100%{
  width: 100%;
  opacity: 1;
}
}



